<template>
    <div class="box">
        <nav-bar :title="title" />
        <div class="content">
            <div class="current-grade">
                <div :style="{ backgroundImage: `url(${bgImg})` }"><img :src="require(`@/assets/upLv0${origin}.png`)" /></div>
                <div>
                    <span>{{ $t('currentLevel') }}</span>
                    <div>
                        <!-- <span v-text="`V${origin <= 0 ? 'IP' : origin}`"></span> -->
                        <span v-text="`M${origin || 'IP'}`"></span>
                        <img src="@/assets/upLv00.png" />
                        <img :src="require(`@/assets/upLv01${origin < 1 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/upLv02${origin < 2 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/upLv03${origin < 3 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/upLv04${origin < 4 ? '-none' : ''}.png`)" />
                        <img :src="require(`@/assets/upLv05${origin < 5 ? '-none' : ''}.png`)" />
                    </div>
                </div>
            </div>
            <slot />
            <div class="privileges" v-if="origin >= 2">
                <div class="title">
                    <span>{{ $t('tequan') }}</span>
                    <span>M1</span>
                </div>
                <div class="count">
                    <div>
                        <img v-for="(i,k) in v1Total" :key="k" :src="require(`@/assets/upLv01${i <= v1Count ? '' : '-not'}.png`)" />
                    </div>
                    <p>{{ v1Count }}<span>/{{ v1Total }}</span></p>
                </div>
                <textarea v-model="v1Address" maxlength="44" :placeholder="$t('friendWallet')" />
                <Button :disabled="v1Count === 0" @click="share(1)" color="linear-gradient(41deg, #F5D755, #FFA271)">{{ $t('tequan') }}</Button>
            </div>
            <div class="privileges" v-if="origin >= 3">
                <div class="title">
                    <span>{{ $t('tequan') }}</span>
                    <span>M2</span>
                </div>
                <div class="count">
                    <div>
                        <img v-for="(i,k) in v2Total" :key="k" :src="require(`@/assets/upLv02${i <= v2Count ? '' : '-not'}.png`)" />
                    </div>
                    <p>{{ v2Count }}<span>/{{ v2Total }}</span></p>
                </div>
                <textarea v-model="v2Address" maxlength="44" :placeholder="$t('friendWallet')" />
                <Button :disabled="v2Count === 0" @click="share(2)" color="linear-gradient(41deg, #F5D755, #FFA271)">{{ $t('tequan') }}</Button>
            </div>
        </div>
    </div>
</template>

<script>
    import { Button, Toast } from 'vant';

    export default {
        name: 'grade-box',
        components: {
            Button,
            'nav-bar': () => import(/* WebpackChunkName: 'grade' */ './nav-bar')
        },
        props: {
            title: {
                type: String,
                required: true
            },
            origin: {
                type: Number,
                default: 0
            },
            supernode: Number
        },
        computed: {
            bgImg() {
                if (!this.supernode || this.supernode < 6) {
                    return require('@/assets/upLvTopBg.png');
                }
                return require('@/assets/supernode.png');
            },
            v1Total() {
                if (this.supernode && this.supernode >= 6) {
                    return 10;
                }

                switch (this.origin) {
                    case 2:
                        return 2;
                    case 3:
                        return 4;
                    default:
                        return 0;
                }
            },
            v2Total() {
                if (this.supernode && this.supernode >= 6) {
                    return 5;
                }

                switch (this.origin) {
                    case 3:
                        return 2;
                    default:
                        return 0;
                }
            }
        },
        data() {
            return {
                v1Count: 0,
                v2Count: 0,
                v1Address: '',
                v2Address: '',
            }
        },
        methods: {
            async share(grade) {
                const address = grade === 1 ? this.v1Address : this.v2Address;
                if (!window.tronWeb.isAddress(address)) {
                    return Toast.fail('地址格式错误！');
                }

                const currentGrade = await this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
                    method: 'levelOf(address)',
                    parameter: [{ type: "address", value: address }]
                }).then(result => result.constant_result[0].decode(['uint256']).toNumber());
                
                if (currentGrade >= grade) {
                    return Toast.fail('此用户已为M' + currentGrade);
                }
                
                Toast.loading({
					message: 'loading...',
					forbidClick: true,
					duration: 0
				});

                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'useGiftDLevel(uint256,address)',
                    parameter: [
                        { type: 'uint256', value: grade },
                        { type: 'address', value: address }
                    ]
				}).then(async result => {
                    await this.$contracts.Manager.useGiftDLevel(grade, address)
                    .send({ feeLimit: '50'.toSun() }).then(hex => {
                        sessionStorage.setItem('transactionHex', hex);

                        const interval = setInterval(() => {
                            window.tronWeb.trx.getTransaction(hex).then(transaction => {
                                if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
                                    sessionStorage.removeItem('transactionHex');
                                    clearInterval(interval);

                                    Toast.clear();
                                    this.v1Address = '';
                                    this.v2Address = '';
                                    this.init();
                                    Toast.success('赠送成功！');
                                }
                            })
                        }, 2000);
                    });
                }).catch(err => {
					Toast.clear();
					console.log(err);
				});
            },
            init() {
                //剩余可赠送等级
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'giftList(address)',
                    parameter: [{ type: 'address', value: this.$address }]
				}).then(result => {
                    const [, V1, V2] = result.constant_result[0].decode(['uint256[]']);

                    this.v1Count = V1 ? V1.toNumber() : 0;
                    this.v2Count = V2 ? V2.toNumber() : 0;
                }).catch(console.log)
            }
        },
        created() {
            this.$tronWeb && this.init();
        }
    }
</script>

<style lang="scss" scoped>
    .privileges {
        padding: 28px 22px 20px;
        border-radius: 20px;
        background-color: #E8E8E8;
        box-shadow: 0px 2px 4px rgba(#000, .3);
        margin-bottom: 62px;
        &>button {
            width: 100%;
            height: 90px;
            color: #B56900 !important;
            font-size: 44px;
            border-radius: 8px;
            box-shadow: 0 6px 12px rgba(#000, .16);
        }
        &>textarea {
            width: 100%;
            height: 160px;
            resize: none;
            border: none;
            font-size: 40px;
        }
        &>.count {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 12px 0 32px;
            &>p {
                color: #FFC533;
                font-size: 40px;
                &>span {
                    color: #656363;
                }
            }
            &>div {
                display: flex;
                align-items: center;
                &>img {
                    width: 40px;
                    height: 40px;
                    margin-right: 20px;
                }
            }
        }
        &>.title {
            color: #656363;
            font-size: 40px;
            text-align: left;
            &>span:last-child {
                color: #FFC533;
                margin-left: 20px;
            }
        }
    }
    .current-grade {
        display: flex;
        align-items: center;
        &>div {
            &:last-child {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                color: #656363;
                font-size: 40px;
                &>div {
                    display: flex;
                    align-items: center;
                    margin-top: 8px;
                    &>img {
                        width: 40px;
                        height: 40px;
                        margin-left: 20px;
                    }
                }
            }
            &:first-child {
                width: 143px;
                height: 125px;
                background-size: 100% 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 22px;
                &>img {
                    width: 54px;
                    height: 52px;
                }
            }
        }
    }
    .content {
        padding: 30px 40px 0;
    }
</style>